export const moment = {
  StringToDate: (str) => {
    return new Date(str)
  }
}
export const gasconsumptioncalculator = {
  calc: (jahresverbrauch, brennnwert, zustandszahl) => {
    /* brennnwert = 8.0 float */
    /* zustandszahl = 0.95 float */
    return jahresverbrauch / (brennnwert * zustandszahl)
  }
}

