<template>
  <div
    v-if="openweather"
    @animationstart="animationStart"
    @animationend="animationEnd"
    @click="showMore"
    class="d-flex flex-column animated temp vw-100"
    ref="window"
    :class="windowCssClass"
  >
    <div ref="panel" class="pt-2 pb-2 bg-transparent">
      <div class="d-flex justify-content-around">
        <div>
          <span class="mt-4 mb-4"
            ><img width="100px" :src="image(icon)" @load="setProperties"
          /></span>
          <span class="display-1 mb-4">{{ parseInt(current) }} &#176;</span>
        </div>
      </div>
      <div class="ps-3 d-flex d-flex justify-content-between">
        <shortLayout title="MaxTemp" :value="max" code="°" />
        <shortLayout title="MinTemp" :value="min" code="°" />
      </div>
      <div class="ps-3 d-flex justify-content-between">
        <shortLayout title="Humidity" :value="humidity" code="%" />
        <shortLayout title="RealFeel" :value="feels_like" code="°" />
      </div>
    </div>
    <div
      class="d-flex flex-column flex-nowrap vw-100 flex-grow-1 pt-1 ps-3 pe-3"
      style="margin: 0px; scroll-snap-type: x mandatory"
    >
      <div
        v-for="item in openweather.data"
        :key="item.dt"
        class="d-flex flex-row align-items-center mb-3 detail"
        style="min-width: 40vw; scroll-snap-align: center"
      >
        <span class="p-3">
          {{ $moment.StringToDate(item.dt_txt).getHours() }} Uhr</span
        >
        <span><img width="100px" :src="image(item.weather[0].icon)" /></span>
        <span class=""> {{ item.weather[0].description }}</span>
        <span class="p-3" style="min-width: 80px">
          {{ parseInt(item.main.temp) }} &#176;</span
        >
      </div>
    </div>
    <div
      class="position-absolute bottom-0 end-0 overflow-hidden"
      style="height: 80px; width: 120px"
    >
      <button
        class="animated btn btn-primary rounded-circle m-1 "
        :class="{ fadeInRight: showmore, fadeOutRight: !showmore }"
      >
        <RefreshCwIcon @click="reload" />
      </button>
    </div>
  </div>
</template>

<script>
import shortLayout from './shortLayout.vue'
import axios from '../../node_modules/axios'
import { RefreshCwIcon } from '../../node_modules/vue-feather-icons'

export default {
  components: { shortLayout, RefreshCwIcon },
  name: 'CurrentWeather',
  data () {
    return {
      max: 0,
      min: 0,
      current: 0,
      icon: '',
      humidity: 0,
      feels_like: 0,
      showmore: false,
      windowCssClass: '',
      openweather: {
        url: '',
        timestamp: null,
        data: [],
        sunrise: null,
        sunset: null
      }
    }
  },
  methods: {
    showMore: function () {
      this.showmore = !this.showmore
      this.windowCssClass = this.showmore ? 'expand' : 'colapse'
    },
    image: function (code) {
      return 'http://openweathermap.org/img/wn/' + code + '@2x.png'
    },
    animationStart: function (type) {
      if (type.animationName.indexOf('expand') !== -1) {
        this.$refs.window.style.zIndex = 3
      }
    },
    animationEnd: function (type) {
      if (type.animationName.indexOf('colapse') !== -1) {
        this.$refs.window.style.zIndex = undefined
      }
    },
    maxTemp: function (list) {
      list.forEach((element) => {
        if (this.max < element.main.temp_max) {
          this.max = parseInt(element.main.temp_max)
        }
        if (this.min === 0) {
          this.min = element.main.temp_min
        }
        if (this.humidity === 0) {
          this.humidity = parseInt(element.main.humidity)
        }
        if (this.min > element.main.temp_min) {
          this.min = parseInt(element.main.temp_min)
        }
        if (this.current === 0) {
          this.current = element.main.temp
        }
        if (this.feels_like === 0) {
          this.feels_like = parseInt(element.main.feels_like)
        }
        if (this.icon === '') {
          this.icon = element.weather[0].icon
        }
      })
    },
    setProperties: function () {
      const boundingElm = this.$refs.panel.getBoundingClientRect()
      this.$refs.window.style.setProperty(
        '--max-height',
        this.$refs.panel.clientHeight + 'px'
      )
      this.$refs.window.style.setProperty(
        '--offset-top',
        0 - boundingElm.y + 'px'
      )
    },
    weather: function () {
      axios
        .get(
          this.openweather.url
        )
        .then((response) => {
          this.maxTemp(response.data.list)
          this.$settings.set({
            openweather: {
              timestamp: new Date().getTime(),
              data: response.data.list,
              sunrise: response.data.city.sunrise,
              sunset: response.data.city.sunset
            }
          })
        })
    },
    reload: function () {
      this.weather()
    }
  },
  watch: {
    openweather (newValue, oldValue) {
      this.maxTemp(newValue.data)
    }
  },
  created () {
    window.addEventListener('resize', this.setProperties)
  },
  destroyed () {
    window.removeEventListener('resize', this.setProperties)
  },
  mounted () {
    const data = this.$settings.get()
    this.openweather = data.openweather
    // this.weather()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.animated.temp {
  --max-height: 293px;
  --offset-top: -56px;
  --offset-bottom: calc(var(--max-height) - 100vh);
  height: var(--max-height);
  max-height: var(--max-height);
  background-color: var(--bs-gray-100);
  overflow: hidden;
}

.btn {
  margin: -10px;
  padding: 10px;
  height: 50px;
  width: 50px;
}

.expand {
  animation-duration: 0.5s;
  animation-name: expand;
  border: 1px solid #e9e9e9;
}

.colapse {
  animation-duration: 0.5s;
  animation-name: colapse;
}

.fadeInRight {
  animation-delay: 0.5s;
  animation-name: bounceInRight;
}

.fadeOutRight {
  animation-delay: 0.5s;
  animation-name: bounceOutRight;
}
</style>
