<template>
  <div
    v-if="google"
    ref="task"
    :class="classList"
    @animationstart="animationStart"
    @animationend="animationEnd"
    @click="allTasks = !allTasks"
  >
    <!--Add buttons to initiate auth sequence and sign out-->
    <div class="display-6 text-start mb-4" >Appointments</div>
    <div
      class="mb-4 d-flex flex-row event"
      v-for="(task, index) in google.data"
      :key="task.id"
      :class="{ 'd-none': index > 2 && !allTasks }"
    >
      <span class="line rounded me-3 mb-2 mt-2" />
      <div class="d-flex flex-column flex-grow-1">
        <span class="text-start">{{ task.summary }}</span>
        <span class="text-end text-secondary">{{
          $moment.StringToDate(task.start).toLocaleDateString()
        }}</span>
      </div>
    </div>
    <div
      class="position-absolute bottom-0 end-0 overflow-hidden"
      style="height: 80px; width: 120px"
    >
      <button
        class="animated btn btn-primary rounded-circle m-1"
        :class="{ fadeInRight: allTasks, fadeOutRight: !allTasks }"
      >
        <RefreshCwIcon @click="handleAuthClick" />
      </button>
    </div>
  </div>
  <div v-else>
    <div class="spinner-border" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
  </div>
</template>

<script>
import { events } from '../assets/google.helper.js'
import { RefreshCwIcon } from '../../node_modules/vue-feather-icons'

export default {
  components: { RefreshCwIcon },
  name: 'TasksComponent',
  data () {
    return {
      info: null,
      allTasks: false,
      init: true,
      classList: [
        'p-3',
        'd-flex',
        'flex-column',
        'animated',
        'list',
        'flex-grow-1'
      ],
      google: {
        client_id: '',
        api_key: '',
        data: []
      }
    }
  },
  methods: {
    readed: function (newData) {
      this.$settings.set({ google: { data: newData } })
    },
    /**
     *  Sign in the user upon button click.
     */
    handleAuthClick: function () {
      events.load(this.google.client_id, this.google.api_key, this.readed)
    },
    animationStart: function (type) {
      if (type.animationName.indexOf('expand') !== -1) {
        this.$refs.task.style.zIndex = 3
      }
    },
    animationEnd: function (type) {
      if (type.animationName.indexOf('colapse') !== -1) {
        this.$refs.task.style.zIndex = 'unset'
      }
    },
    setProperties: function () {
      const boundingElm = this.$refs.task.getBoundingClientRect()
      this.$refs.task.style.setProperty(
        '--max-height',
        this.$refs.task.clientHeight + 'px'
      )
      this.$refs.task.style.setProperty(
        '--offset-top',
        0 - boundingElm.y + 'px'
      )
      console.log(boundingElm)
    }
  },
  watch: {
    allTasks (showAll, oldValue) {
      const eIndex = this.classList.indexOf('expand')
      const cIndex = this.classList.indexOf('colapse')
      if (eIndex === -1 && cIndex === -1) {
        this.setProperties()
      }
      if (showAll) {
        if (cIndex !== -1) {
          this.classList.splice(cIndex, 1)
        }
        this.classList.push('expand')
      } else {
        if (eIndex !== -1) {
          this.classList.splice(eIndex, 1)
        }
        this.classList.push('colapse')
      }
    }
  },
  mounted () {
    const data = this.$settings.get()
    this.$data.google = data.google
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.animated.list {
  --max-height: auto;
  --offset-top: 0px;
  --offset-bottom: 0;
  background-color: var(--bs-gray-100);
  overflow: hidden;
}

.event .line {
  background-color: var(--bs-purple);
  min-width: 5px;
}
.text-secondary {
  font-size: 80%;
}

.btn.rounded-circle {
  margin: -10px;
  padding: 10px;
  height: 50px;
  width: 50px;
}

.expand {
  animation-duration: 0.3s;
  animation-name: expand;
  border: 1px solid #e9e9e9;
}

.colapse {
  animation-duration: 0.3s;
  animation-name: colapse;
}

.fadeInRight {
  animation-delay: 0.5s;
  animation-name: bounceInRight;
}

.fadeOutRight {
  animation-delay: 0.5s;
  animation-name: bounceOutRight;
}
</style>
